@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Helvetica;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  margin: 30px auto;
  overflow: auto;
  padding: 20px;
  border-radius: 30px;
  background-color: black;
}

.btn {
  display: inline-block;
  width: 80px;
  height: 80px;
  border: none;
  margin: 5px;
  border-radius: 50%;
  cursor: pointer;
  text-decoration: none;
  font-size: 30px;
  padding: 5px;
  vertical-align: middle;
}

.btn.Etc {
  background-color: grey;
}

.btn.Val {
  background-color: #38393d;
  color: white;
  font-size: 35px;
}

.Icon {
  position: relative;
  top: 5%;
  width: 50%;
}

.btn.Val.zero {
  width: 170px;
  border-radius: 40px;
  background-color: #38393d;
}

.btn:hover {
  background: 'red';
}

.btn:active {
  filter: brightness(1.3);
}

.screen {
  position: relative;
  height: 125px;
}

.MainText {
  color: white;
  font-size: 50px;

  position: absolute;
  bottom: 20px;
  right: 20px;
}